//路由基本配置
function PrivateRoute({ component:Com, ...rest }) {
	// console.log(routes);
	// console.log(rest);
	// console.log(this.props);
	// if(rest.routes){
	// 	// console.log('if');
	// 	return PrivateRoute({component:Com, ...rest.routes});
	// }else{
		// console.log('else');
		return (
			<Route
				{...rest}
				render={({ location }) =>
				localStorage.getItem('userinfo')
				?<Com {...rest}/> 
				: (<Redirect to={{pathname: "/login", state: location }}/>)
				}
			/>
		);
	// }
	
  }
  function RouteWithSubRoutes(route) {
	//   console.log(route);
	//   this.props.route = route;
	return (route.pri? <PrivateRoute {...route}/>:<Route
		path={route.path}
		render={props => (
		  // pass the sub-routes down to keep nesting
		  <route.component {...props} routes={route.routes} />
		)}
	  />
	);
  }

const Reactt = ()=>{
	return <Router>
		<Header />
		<Switch>
			{
				routes.map((route, index) => (
					<RouteWithSubRoutes key={index} {...route} />
					// route.pri ? <PrivateRoute key={index} {...route}/>: <Route key={index} {...route}/>
				))
			}
		</Switch>
	</Router>
}
export {RouteWithSubRoutes}
export default Reactt;



