import React from 'react'

const Login = (props) => {
	const login = () => {
		localStorage.setItem('userinfo', 'tom')
		//存完以后，跳到首页
		props.history.replace(props.location.state)
	}
    return (
        <div>
			<button onClick={login}>登陆</button>
        </div>
    )
}

export default Login
