import Home from './pages1/Home';
import Doc from './pages1/Doc';
import Tuto from './pages1/Tuto';
import Blog from './pages1/Blog';
import Comm from './pages1/Comm';
import Login from './pages1/Login';
import {Core, Guide, Api, Hooks} from './pages1/Doc';

const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/doc',
        exact: false,
        component: Doc,
        pri: true,
        routes: [
            {
                path:'/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            },
        ]
    },
    {
        path: '/tuto',
        exact: false,
        component: Tuto,
        pri: true
    },
    {
        path: '/blog',
        exact: false,
        component: Blog
    },
    {
        path: '/comm',
        exact: false,
        component: Comm
    },
    {
        path: '/login',
        exact: false,
        component: Login
    },
]


export default routes